import React from 'react';

import { Shape } from '../Shape/Shape';

export const RenderManyShapes = ({ shapeNum }: { shapeNum: number }) => {
	const shapesArray = [];
	let index = 0;

	for (let i = 0; i < shapeNum; i++) {
		shapesArray.push(i);
	}

	return (
	    <>
            {
                shapesArray.map((shape) => (
                    <Shape
                        key={index++}
                        position={
                            shape % 2
                                ? [Math.random(), Math.random(), Math.random()]
                                : shape % 3
                                ? [-Math.random(), -Math.random(), -Math.random()]
                                : shape % 4
                                    ? [-Math.random(), Math.random(), Math.random()]
                                    : [Math.random(), -Math.random(), +Math.random()]
                        }
                        rotation={[
                            Math.random() / Math.random() / 100,
                            Math.random() / Math.random() / 100,
                            Math.random() / Math.random() / 100,
                        ]}
                    />
                ))
            }
        </>
    );
};
